// pages / dashboard / account / view

// Modules
import React from "react";
import {connect} from 'react-redux';
import Helmet from 'react-helmet';
import Link, {navigateTo} from "gatsby-link";

// Styles
// import styles from '../../../style/preloader.module.css';

// Components
// import ConversationSidebar from '../../../components/dashboard/conversation/ConversationSidebar';
// import ChatMessage from '../../../components/dashboard/conversation/ChatMessage';


class AccountView extends React.Component {
    componentDidMount() {
        $(".select-acc-type").select2({
            placeholder: "Select Account Type",
            allowClear: true,
            width: '100%'
        });

        $(".select-property-type").select2({
            placeholder: "Select Property Type",
            allowClear: true,
            width: '100%'
        });
    }

    render() {
        if (this.props.user.loggedIn) {

            return (
                <span>

                    {/* Start of Helmet */}
                    <Helmet title="Account - view" meta={[
                        {
                            name: 'description',
                            content: 'Account - view'
                        }, {
                            name: 'keywords',
                            content: 'Account'
                        }
                    ]}/>
                    {/* End of Helmet */}

                    {/* Start of Heading */}
                    <div className="row wrapper border-bottom white-bg page-heading">
                        <div className="col-lg-10">
                            <h2>View Account</h2>
                            <ol className="breadcrumb">
                                <li>
                                    <Link to="/dashboard">Home</Link>
                                </li>
                                <li className="active">
                                    <strong>View Account</strong>
                                </li>
                            </ol>
                        </div>
                        <div className="col-lg-2">

                        </div>
                    </div>
                    {/* End of Heading */}


                    {/* Start of Blueimp Gallery Lightbox */}
                    <div id="blueimp-gallery" className="blueimp-gallery blueimp-gallery-controls">
                        <div className="slides"></div>
                        <h3 className="title"></h3>
                        <a className="prev"></a>
                        <a className="next"></a>
                        <a className="close"></a>
                        <a className="play-pause"></a>
                        <ol className="indicator"></ol>
                    </div>
                    {/* End of Blueimp Gallery Lightbox */}


                    {/* Start of Content */}
                    <div className="wrapper wrapper-content animated fadeInRight">
                        <div className="row">
                            <div className="col-md-6">
                                <div className="profile-image">
                                    <img src="http://webapplayers.com/inspinia_admin-v2.7.1/img/a4.jpg" className="img-circle circle-border m-b-md" alt="profile" />
                                </div>
                                <div className="profile-info">
                                    <div className="">
                                        <div>
                                            <h2 className="no-margins">
                                                Alex Smith
                                            </h2>
                                            <h4>Unit A-02-13</h4>
                                            <small>
                                                There are many variations of passages of Lorem Ipsum available, but the majority
                                                have suffered alteration in some form Ipsum available.
                                            </small>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="clearfix"></div>

                            <div className="col-md-3">
                                <div className="ibox">
                                    <div className="ibox-content">
                                        <h3>About Alex Smith</h3>
                                        <div className="space-10"></div>

                                        <p className="small">
                                            <i className="fa fa-phone"></i> 01831212122
                                            <br/><br/>
                                            <i className="fa fa-envelope"></i> lisalimbert@gmail.com
                                        </p>
                                        <div className="space-25"></div>

                                        <p className="small">
                                            There are many variations of passages of Lorem Ipsum available, but the majority have suffered alteration in some form, by injected humour, or randomised words which don't.
                                            <br/>
                                            <br/> If you are going to use a passage of Lorem Ipsum, you need to be sure there isn't anything embarrassing
                                        </p>

                                    </div>
                                </div>
                            </div>

                            <div className="col-md-4">
                                <div className="ibox float-e-margins">
                                    <div className="ibox-content">
                                        <h3>Photos</h3>

                                        <div className="lightBoxGallery">
                                            <a href="http://webapplayers.com/inspinia_admin-v2.7.1/img/gallery/1.jpg" title="Demo Image" data-gallery="">
                                                <img src="http://webapplayers.com/inspinia_admin-v2.7.1/img/gallery/1s.jpg" />
                                            </a>
                                            <a href="http://webapplayers.com/inspinia_admin-v2.7.1/img/gallery/2.jpg" title="Demo Image" data-gallery="">
                                                <img src="http://webapplayers.com/inspinia_admin-v2.7.1/img/gallery/2s.jpg" />
                                            </a>
                                            <a href="http://webapplayers.com/inspinia_admin-v2.7.1/img/gallery/3.jpg" title="Demo Image" data-gallery="">
                                                <img src="http://webapplayers.com/inspinia_admin-v2.7.1/img/gallery/3s.jpg" />
                                            </a>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div className="clearfix"></div>

                            <div className="col-lg-10 col-hcenter">
                                <div className="ibox float-e-margins">
                                    <div className="ibox-title">
                                        <h5>Account Configuration</h5>
                                        <div className="ibox-tools">
                                            <a className="collapse-link">
                                                <i className="fa fa-chevron-up"></i>
                                            </a>
                                            <a className="dropdown-toggle" data-toggle="dropdown" href="#">
                                                <i className="fa fa-wrench"></i>
                                            </a>
                                            <ul className="dropdown-menu dropdown-user">
                                                <li><a href="#">Config option 1</a>
                                                </li>
                                                <li><a href="#">Config option 2</a>
                                                </li>
                                            </ul>
                                            <a className="close-link">
                                                <i className="fa fa-times"></i>
                                            </a>
                                        </div>
                                    </div>
                                    <div className="ibox-content">
                                        <form className="form-horizontal">
                                            <h2>Account Personal Details</h2>
                                            <small>(*) field are required to be filled in</small>
                                            <div className="space-25"></div>
                                            <div className="form-group">
                                                <label className="col-lg-2 control-label" htmlFor="firstName">First Name *</label>
                                                <div className="col-lg-10">
                                                    <input type="text" placeholder="First Name" id="firstName" className="form-control" defaultValue="Alex" required />
                                                </div>
                                            </div>
                                            <div className="form-group">
                                                <label className="col-lg-2 control-label" htmlFor="lastName">Last Name *</label>
                                                <div className="col-lg-10">
                                                    <input type="text" placeholder="Last Name" id="lastName" className="form-control" defaultValue="Smith" required />
                                                </div>
                                            </div>
                                            <div className="form-group">
                                                <label className="col-lg-2 control-label" htmlFor="lastName">Username *</label>
                                                <div className="col-lg-10">
                                                    <input type="text" placeholder="Last Name" id="lastName" className="form-control" defaultValue="alexsmith" required />
                                                </div>
                                            </div>
                                            <div className="form-group">
                                                <label htmlFor="accType" className="col-lg-2 control-label">Account Type *</label>
                                                <div className="col-lg-10">
                                                    <select className="select-acc-type form-control" id="accType" defaultValue="Resident" disabled required >
                                                        <option></option>
                                                        <option value="Support">Support</option>
                                                        <option value="Manager">Manager</option>
                                                        <option value="Admin">Admin</option>
                                                        <option value="Resident">Resident</option>
                                                        <option value="Guard">Guard</option>
                                                    </select>
                                                </div>
                                            </div>
                                            <div className="form-group">
                                                <label htmlFor="propertyType" className="col-lg-2 control-label">Property *</label>
                                                <div className="col-lg-10">
                                                    <select className="select-property-type form-control" id="propertyType" defaultValue="Banyan Tree, KLCC - Jalan Conlay, Klcc, 55100, Kuala Lumpur" disabled required >
                                                        <option></option>
                                                        <option value="Banyan Tree, KLCC - Jalan Conlay, Klcc, 55100, Kuala Lumpur">Banyan Tree, KLCC - Jalan Conlay, Klcc, 55100, Kuala Lumpur</option>
                                                        <option value="Agile Mont Kiara - Jalan Duta Kiara, Taman Sri Hartamas, 50480, Kuala Lumpur">Agile Mont Kiara - Jalan Duta Kiara, Taman Sri Hartamas, 50480, Kuala Lumpur</option>
                                                        <option value="casaman - Desa ParkCity, casaman, Kuala Lumpur">casaman - Desa ParkCity, casaman, Kuala Lumpur</option>
                                                        <option value="The Breezeway - Jalan Residen 2, Desa ParkCity, Kuala Lumpur, Desa ParkCity, 52200, Kuala Lumpur">The Breezeway - Jalan Residen 2, Desa ParkCity, Kuala Lumpur, Desa ParkCity, 52200, Kuala Lumpur</option>
                                                        <option value="M Vertica - Kuala Lumpur, Cheras, Kuala Lumpur">M Vertica - Kuala Lumpur, Cheras, Kuala Lumpur</option>
                                                    </select>
                                                </div>
                                            </div>
                                            <div className="form-group">
                                                <label className="col-lg-2 control-label" htmlFor="address">Unit No *</label>
                                                <div className="col-lg-10">
                                                    <input type="text" placeholder="Unit No" id="address" defaultValue="Unit A-02-13" className="form-control" required />
                                                </div>
                                            </div>
                                            <div className="form-group">
                                                <label className="col-lg-2 control-label" htmlFor="email">Email</label>
                                                <div className="col-lg-10">
                                                    <input type="email" placeholder="Email" id="email" className="form-control" defaultValue="alexsmith@gmail.com" />
                                                </div>
                                            </div>
                                            <div className="form-group">
                                                <label className="col-lg-2 control-label" htmlFor="address">About User</label>
                                                <div className="col-lg-10">
                                                    <textarea className="form-control textarea-no-resize" rows="4" placeholder="About User" ref="message" maxLength="255" defaultValue="User enjoy travelling and fishing." ></textarea>
                                                </div>
                                            </div>

                                            <div className="form-group">
                                                <div className="col-sm-4 col-sm-offset-2 pull-right">
                                                    <button className="btn btn-primary" type="submit">Save Changes to Personal Details</button>
                                                </div>
                                            </div>
                                      </form>


                                      <div className="hr-line-dashed"></div>
                                      <div className="space-30"></div>

                                      <form className="form-horizontal">
                                            <h2>Reset Password for this account</h2>
                                            <small>(*) field are required to be filled in</small>
                                            <div className="space-25"></div>
                                            <div className="form-group">
                                                <label className="col-lg-2 control-label" htmlFor="newPassword">Password *</label>
                                                <div className="col-lg-10">
                                                    <input type="password" placeholder="Password" id="newPassword" className="form-control" required />
                                                </div>
                                            </div>

                                            <div className="form-group">
                                                <label className="col-lg-2 control-label" htmlFor="newPassword">Confirm Password *</label>
                                                <div className="col-lg-10">
                                                    <input type="password" placeholder="Confirm Password" id="newPassword" className="form-control" required />
                                                </div>
                                            </div>

                                            <div className="form-group">
                                                <div className="col-sm-4 col-sm-offset-2 pull-right">
                                                    <button className="btn btn-info" type="submit">Proceed to Reset Password</button>
                                                </div>
                                            </div>
                                      </form>


                                      <div className="hr-line-dashed"></div>
                                      <div className="space-30"></div>

                                      <form className="form-horizontal">
                                            <h2>Grant Access</h2>
                                            <div className="space-25"></div>
                                            <div className="form-group">
                                                <label className="col-lg-2 control-label" htmlFor="checkbox4">Enable Access to</label>
                                                <div className="col-lg-10">
                                                    <div className="row">
                                                        <div className="col-lg-4">
                                                            <div className="checkbox checkbox-info">
                                                                <input id="checkboxDashboard" type="checkbox" defaultChecked="true" />
                                                                <label htmlFor="checkboxDashboard">
                                                                    Dashboard
                                                                </label>
                                                            </div>
                                                        </div>
                                                        <div className="col-lg-4">
                                                            <div className="checkbox checkbox-info">
                                                                <input id="checkboxMyResidence" type="checkbox" defaultChecked="true" />
                                                                <label htmlFor="checkboxMyResidence">
                                                                    My Residence
                                                                </label>
                                                            </div>
                                                        </div>
                                                        <div className="col-lg-4">
                                                            <div className="checkbox checkbox-info">
                                                                <input id="checkboxConversation" type="checkbox" defaultChecked="true" />
                                                                <label htmlFor="checkboxConversation">
                                                                    Conversation
                                                                </label>
                                                            </div>
                                                        </div>
                                                        <div className="col-lg-4">
                                                            <div className="checkbox checkbox-info">
                                                                <input id="checkboxSecurityCameras" type="checkbox" defaultChecked="true" />
                                                                <label htmlFor="checkboxSecurityCameras">
                                                                    Security Cameras
                                                                </label>
                                                            </div>
                                                        </div>
                                                        <div className="col-lg-4">
                                                            <div className="checkbox checkbox-info">
                                                                <input id="checkboxPayment" type="checkbox" defaultChecked="true" />
                                                                <label htmlFor="checkboxPayment">
                                                                    Payment
                                                                </label>
                                                            </div>
                                                        </div>
                                                        <div className="col-lg-4">
                                                            <div className="checkbox checkbox-info">
                                                                <input id="checkboxPaymentHistory" type="checkbox" defaultChecked="true" />
                                                                <label htmlFor="checkboxPaymentHistory">
                                                                    Payment History
                                                                </label>
                                                            </div>
                                                        </div>
                                                        <div className="col-lg-4">
                                                            <div className="checkbox checkbox-info">
                                                                <input id="checkboxForum" type="checkbox" defaultChecked="true" />
                                                                <label htmlFor="checkboxForum">
                                                                    Forum
                                                                </label>
                                                            </div>
                                                        </div>
                                                        <div className="col-lg-4">
                                                            <div className="checkbox checkbox-info">
                                                                <input id="checkboxFacilityBooking" type="checkbox" defaultChecked="true" />
                                                                <label htmlFor="checkboxFacilityBooking">
                                                                    Facility Booking
                                                                </label>
                                                            </div>
                                                        </div>
                                                        <div className="col-lg-4">
                                                            <div className="checkbox checkbox-info">
                                                                <input id="checkboxSettings" type="checkbox" defaultChecked="true" />
                                                                <label htmlFor="checkboxSettings">
                                                                    Settings
                                                                </label>
                                                            </div>
                                                        </div>


                                                        <div className="col-lg-4">
                                                            <div className="checkbox checkbox-info">
                                                                <input id="checkboxSettings" type="checkbox" defaultChecked="true" />
                                                                <label htmlFor="checkboxSettings">
                                                                    <strong>*Property Mgmt</strong>
                                                                </label>
                                                            </div>
                                                        </div>

                                                    </div>
                                                </div>
                                            </div>


                                            <div className="hr-line-dashed"></div>
                                            <div className="form-group">
                                                <div className="col-sm-4 col-sm-offset-2 pull-right">
                                                    <button className="btn btn-primary" type="submit">Save Changes to Grant Access</button>
                                                </div>
                                            </div>
                                        </form>

                                        <div className="hr-line-dashed"></div>
                                        <div className="space-30"></div>

                                        <form className="form-horizontal">
                                              <h2 className="text-danger">Delete this Account</h2>
                                              <strong>
                                                  <small>Please be note! Once this account is delete, all its information will also be deleted. This action is irreversible.</small>
                                              </strong>
                                              <div className="space-25"></div>

                                              <div className="form-group">
                                                  <div className="col-sm-4 col-sm-offset-2 pull-right">
                                                      <button className="btn btn-danger" type="submit">Proceed to Delete Account</button>
                                                  </div>
                                              </div>
                                        </form>


                                    </div>
                                </div>
                            </div>


                        </div>
                    </div>
                    {/* End of Content */}


                </span>
            );
        } else {
            return null;
        }
    }
};

const mapStateToProps = (state) => {
    return {user: state.users.user};
}

const mapDispatchToProps = (dispatch) => {
    return {};
}

export default connect(mapStateToProps, mapDispatchToProps())(AccountView);



